<template>
<section class="wrap has_header_wrap">
	
	<!-- 头部标题 -->
	<com-header titles="需求登记">
		<template  slot="style-def"></template>
	</com-header>
	<!-- 头部标题 end -->

	<section class='wrap_in toregister' :class="{ unscrollable: isUnscRollable }">	
		<div class="trhead">温馨提示：提交登记后，阿康门店药师将在1个小时内致电与您确认，请您保持电话通畅哦。</div>
		
		<div class="pro">
			<div class="item d_flex">
				<div class="img">
					<img :src="productData.productPhotos | filterImgs" :alt="productData.productName">
					
					<!-- <img v-if="!productData.productPhotos.length==0" src="../../../static/images/ak.png" alt=""> -->
					<!-- <img v-if="productData.productPhotos.length>0" v-lazy="productData.productPhotos[0].photoURL" :alt="productData.productName"> -->
				</div>
				<div class="right">
					<h2 class="d_flex"><span>{{ productData.productName }}</span><span>￥{{ productData.ourPrice }}</span></h2>
					<span>{{ productData.spec }}</span>
				</div>
			</div>
		</div>
		<!-- 规格 -->
		<div class="spec d_flex">
			<div class="left">
				规格
			</div>
			<ul class="right">
				<!-- 多规格 -->
				<li v-if="productData.isMultSpec"
					:class="{ cur: index === curMultIndex }"
					v-for="(pd,index) in productData.productDetailCode"
					@click="chooseMultiSpec(pd,index)">{{ pd.propertyAndValue }}</li>
				<!-- 非多规格 -->
				<li v-if="!productData.isMultSpec"
					class="cur">{{ productData.spec }}</li>	
			</ul>
		</div>
		<!-- 规格 end -->
		<!-- 数量 -->
		<div class="nums d_flex">
			<div class="left">
				数量
			</div>
			<div class="right">
				<button @click="numReduce"></button>
				<input type="text" v-model.trim="num">
				<button @click="numAdd"></button>
			</div>
		</div>
		<!-- 数量 end -->
		<div class="user_info">
			<div>
				<i class="icon_star"></i>
				<mt-field label="姓名" placeholder="请输入名字" v-model="username"></mt-field>
			</div>
			<div>
				<i class="icon_star"></i>
				<mt-field label="手机" placeholder="选择省市区" type="text" v-model="phone"></mt-field>
			</div>
			<div>
				<i class="icon_star"></i>
				<mt-field class="bg_white" label="地区" disabled placeholder="请输入联系手机号" type="text" v-model="address"></mt-field>
			</div>
			<div>
				<i class="icon_star"></i>
				<mt-field label="详细地址" placeholder="请输入详细地址" type="textarea" rows="4" v-model="del_address"></mt-field>
			</div>
			<div>
				<mt-field label="年龄" placeholder="请输入年龄" type="number" v-model="age"></mt-field>
			</div>
			<mt-radio
				class="sexs d_flex"	
				title="性别"
				v-model="sexVal"
				:options="options"></mt-radio>
			<div>
				<mt-field label="身份证号" placeholder="请填写身份证号码" type="number" v-model="id_num"></mt-field>
			</div>
			<div>
				<mt-field label="门店递送" type="text"></mt-field>
			</div>
			<mt-checklist
  title="复选框列表"
  v-model="sexVal"
  :options="['选项A', '选项B', '选项C']">
</mt-checklist>
			<mt-field label="留言" placeholder="请选择需求数量，并留下您的收货地址和其他要求，让我们更好的为您服务" type="textarea" rows="4" v-model="Remarks"></mt-field>
			<div>
				<mt-field label="处方笺" type="text"></mt-field>
			</div>
		</div>
		<p class="ophone">如紧急，可拨打400-9309-990咨询</p>
		<div class="btn_block">
			<button @click="btnConfirm">提交需求</button>
		</div>
	</section>

	<!-- 子页面 -->
    <transition name="fade">
    	<router-view v-on:changeClass="changeClassOne"></router-view>
    </transition>
    <!-- 子页面 end -->
</section>
</template>

<script>
import { Field, Toast, MessageBox, Cell, Checklist, Radio } from 'mint-ui'; 

export default {
	name: 'toregister',
	data (){
		return {
			isUnscRollable: false, //是否页面不可滚动
			backParentPage: false, //是否回到父页面
			username: this.$route.query.uName?this.$route.query.uName:'', //用户名
			phone: this.$route.query.uPhone?this.$route.query.uPhone:'', //手机
			Remarks: this.$route.query.uRemark?this.$route.query.uRemark:'', //留言
			productId: this.$route.query.id, //产品ID
			productData: '',//产品信息
		    curMultIndex: this.$route.query.curMultIndex===0 || this.$route.query.curMultIndex?Number(this.$route.query.curMultIndex):'',//当前的多规格index
		    curMult: '',//当前多规格
		    num: this.$route.query.num?this.$route.query.num:1,//数量
		    flag: false,//防止多次提交
			options: [
			  {
			    label: '男',
			    value: 'male'
			  },
			  {
			    label: '女',
			    value: 'female'
			  }
			],
			sexVal: 'male',//选中的性别
		}
	},
	components: {
    	'mt-field': Field,
    	'mt-cell': Cell,
    	'mt-checklist': Checklist,
		'mt-radio': Radio,
	},
	watch: {
		num (){
			var checkNum = /^[0-9]*[1-9][0-9]*$/;

			if( !checkNum.test(this.num) ){
				this.$options.methods.showToast('请填写正确的数量');
				return false;
			}
		},
	},
	filters: {
	   filterImgs ( data ){
	   	// console.log(data)
	   	if( !data ){ return false; };
	   	if( data.length>0 ){
	   		return data[0].photoURL;
	   	}else{
	   		return '../../../static/images/ak.png';
	   	}
	   },
	},
	created (){
		const _self = this;

		this.$ajax({
			// url: '/apihj/product/getProductByProductId',
			url: _self.webRoot + '/product/getProductByProductId',
			data: {
				productId: _self.productId
			}
		})
		.then(function(res){
			// console.log(res)
			if( res.data.success ){
				if( !res.data.data ){
					_self.$options.methods.showToast(res.data.msg);
					return false;
				}
				_self.productData = res.data.data;

				//如果url参数有多规格
				if( _self.$route.query.curMultIndex || _self.$route.query.curMultIndex === 0 ){
					_self.curMult = res.data.data.productDetailCode[_self.$route.query.curMultIndex];
					// console.log(1,_self.curMult,res.data.data.productDetailCode[_self.$route.query.curMultIndex])
				}
					// console.log(2,_self.curMult,res.data.data.productDetailCode[_self.$route.query.curMultIndex])
			}else{
				_self.$options.methods.showToast(res.data.msg);
			}
		})
		.catch(function(err){
			console.log(err)
		})
	},
	updated (){
		
	},
	mounted (){
		this.$emit('changeClass',false);

		gtag('event', '进入需求登记页面', {
		  'event_category' : '商品id: ' + this.productData.productID,
		  'event_label' : '成功'
		});
		
	},
	destroyed (){
		this.$emit('changeClass',true);
	},
	methods: {
		//选择多规格
		chooseMultiSpec (pd,index){
			this.curMultIndex = index;
			this.curMult = pd;
		},
		//数量减少
		numReduce (){
			const _self = this;
			var checkNum = /^[0-9]*[1-9][0-9]*$/;
			if( this.num <= 1 ){
				return false;
			}else{
				if( !checkNum.test(this.num) ){
					this.$options.methods.showToast('请填写正确的数量');
					return false;
				}
				this.num--;
			}
		},
		//数量增加
		numAdd (){
			const _self = this;
			var checkNum = /^[0-9]*[1-9][0-9]*$/;
			if( this.num === 0 || !this.num ){
				this.num = 1;
			}else{
				if( !checkNum.test(this.num) ){
					this.$options.methods.showToast('请填写正确的数量');
					return false;
				}
				this.num++;
			}
		},
		btnConfirm (){
			const _self = this;
			var checkNum = /^[0-9]*[1-9][0-9]*$/;
			var checkPhone = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;

			if( this.flag ){ return false; }
			this.flag = true;
			
			var detailCodeID = '';
			if( this.productData.isMultSpec ){ //开启多规格
				detailCodeID = this.curMult.detailCodeID; //是

				//有开启多规格未选规格的所以暂时不要
				// if( !productNumber ){
				// 	_self.$options.methods.showToast( '请选择规格!' );
				// 	_self.flag = false;
				// 	return false;
				// }
			}
			// console.log('detailCodeID: ', this.curMult);
			// return false;
			if( !this.username ){
				_self.$options.methods.showToast( '请填写姓名!' );
				_self.flag = false;
				return false;	
			}
			if( !this.phone || !checkPhone.test(this.phone) ){
				_self.$options.methods.showToast( '请填写正确的手机号码!' );
				_self.flag = false;
				return false;	
			}

			this.$ajax({
				url: _self.webRoot + '/require/addRequire',
				data: {
					productID: _self.productData.productID,//商品ID
					productName: _self.productData.productName,//商品名称
					productCode: _self.productData.productCode,//商品代码
					detailCodeID: detailCodeID,//多规格ID
					qty: _self.num,//预定数量
					memberID: _self.$store.state.memberId,//会员ID
					// memberCode: _self.$store.state.memberId,//会员账号
					realName: _self.username,//姓名
					phone: _self.phone,//手机号
					remark: _self.Remarks,//留言
					sysNo: 'WAP端',//端
				}
			})
			.then(function(res){
				// console.log(res)
				if( !res.data.success ){
					_self.$options.methods.showToast( res.data.msg );
					_self.flag = false;
					gtag('event', '提交需求登记', {
					  'event_category' : _self.productData.productID,
					  'event_label' : '失败'
					});
					return false;
				}else{
					// _self.$options.methods.showToast( res.data.msg );
					// MessageBox.alert(res.data.msg, '提示');
					gtag('event', '提交需求登记', {
					  'event_category' : _self.productData.productID,
					  'event_label' : '成功'
					});
					MessageBox.alert(res.data.msg, '提示').then(action => {
						setTimeout(function(){
							_self.$router.go(-1);
						},300);
					})
				}
			})
			.catch(function(err){
				console.log(err)
			})
		},
		btnBuy ( id ){
			// console.log(this.activeIndexSlide)
			if( id == 2 ){
				this.$router.push({ path: '/purchase' });
				return false;
			}
			this.$router.push({ path: '/purchase', query: { id: '221' } });
		},
		
		//公共函数
	    //显示提示信息
	    showToast( msg, time ){
	      var time = time ? time : 2000;

	      Toast({
	        message: msg,
	        position: 'center',
	        duration: time
	      });     
	    },
	    // 判断是否返回父页面
	    changeClassOne ( isChildDestroyed ){
	      if( isChildDestroyed ){ 
	        //退出子页面
	        this.backParentPage = true; //返回首页
	        this.isUnscRollable = false;
	      }else{
	        //没退出子页面
	        this.backParentPage = false; // 没返回首页
	        this.isUnscRollable = true;
	      }
	    },
	},
}	
</script>

<style scoped>
@import '../../../static/css/headerTitle.css';
@import './Css/toregister.css';
</style>

<style>
.toregister .user_info .mint-field .mint-cell-title {
	width: 1.25rem;
}

.toregister .user_info .bg_white .mint-field-core {
	background-color:#fff;
}
.toregister .user_info .bg_white .mint-cell-value>input {
	background-image:url(../../../static/images/arr_r1.png);
	background-position:center right;
	background-repeat:no-repeat;
	padding-right:0.4375rem;
}

.toregister .sexs .mint-radiolist-title {
	width:1.25rem;
}
</style>


